@import 'assets/scss/utils/mixins';

/**
* Host element styles
*/
:host.c-btn {
	&--icon {
		// To fit the icon in the sizes declared in the parent container
		display: block;
	}
}

:host.add-favorite {
	.mat-icon {
		color: var(--prim-2) !important;
	}

	.mat-mdc-button:disabled {
		background: transparent !important;
	}
}

:host.delete-account {
	.continue-btn {
		background-color: var(--error-3) !important;
		color: var(--error-1) !important;
	}

	.cancel-btn {
		border: 1px solid var(--general-4) !important;
		color: var(--general-4) !important;
	}

	.delete-btn {
		color: var(--error-3) !important;
		height: auto !important;
	}
}

:host.ads-btn {
	.share-btn {
		color: var(--general-4) !important;
	}

	.ad-header-btn {
		color: var(--general-4) !important;
		width: 40px;
		height: 40px;

		.mat-icon {
			font-size: 18px;
			width: 18px;
			height: 18px;
		}
	}
}
// end - Host element styles

.c-btn {
	font-size: 14px;
	height: 40px;
	min-width: 40px;

	&--filled {
		&.close-round {
			width: 40px;

			border-radius: 100%;
			padding: 0;
			background: var(--general-1);
			color: var(--general-4);

			.mat-icon {
				margin: 0 !important;
				width: 20px;
				height: 20px;
			}
		}
	}

	&.rounded-corner {
		border-radius: 100px;
	}

	// &--stroked {
	// }

	// &--basic {
	// }

	// &--icon {
	// }
}

.download-icon {
	font-size: 20px !important;
	width: 20px !important;
	margin-right: 4px !important;
}

@include media-breakpoint-down(sm) {
	.c-btn {
		font-size: 14px;
		height: 35px;
		min-width: 30px;
	}
}
